<template>
  <div class="lessonCard">
    <div class="lessonCard-content">
      <div class="card-item" @click="gotoEditer">
           <div class="wrap-border">
              <div class="card-adsFlag"></div>
              <div class="postal-card">
                      <div class="card-left">
                          <div class="book-bg" style="background:#eee;"></div>
                      </div>
                      <div class="card-right" style="width:100%;">
                          <div class="cardRight-top" style="background:#eee;width:100%;height:15px;margin-bottom:20px;"></div>
                          <div class="cardRight-middle" style="background:#eee;width:80%;height:15px;margin-bottom:20px;"></div>
                          <div class="cardRight-bottom" style="background:#eee;width:50%;height:15px;margin-bottom:20px;"></div>
                          <div class="cardRight-bottom" style="background:#eee;width:45%;height:30px;"></div>
                      </div>
                  </div>
                  <div class="card-tottom">
                      <div class="just-border">
                          <i class="el-icon-plus"></i><span style="display:inline-block;padding-left:10px;color:#319bd7">新增课程</span>
                      </div>
                  </div>
           </div>
        </div>
     <template  v-for="(card,idx) in list">
        <div class="card-item" :key="idx" @click="gotoDatabaseEditer(card.id)">
            <div class="wrap-border">
                <div class="card-adsFlag">
                        <template v-if="card.examine==-1">
                            <img src="../assets/publishing.png" alt="">
                        </template>
                        <template v-else-if="card.examine==1">
                            <img src="../assets/published.png" alt="">
                        </template>
                        <template v-else>
                            <img src="../assets/unPublish.png" alt="">
                        </template>
                </div>
                <div class="postal-card">
                    <div class="card-left">
                        <div class="book-bg" :style="{backgroundImage:`url(${card.app_pic})`}"></div>
                    </div>
                    <div class="card-right">
                        <div class="cardRight-top">
                            {{card.app_desc}}
                        </div>
                        <div class="cardRight-middle">
                            {{card.app_content_name}}
                        </div>
                        <div class="cardRight-bottom">
                            <div class="price">价格：{{card.price}}</div>
                            <div class="tip" v-if="card.activitys">
                              <div v-for='(activity,idx) in card.activitys' :key="idx" class="tip-item">
                                {{activity.activity_desc}}
                              </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="wrapCard-tottom">
                    <div class="caseNum">
                        <div class="borders rightSide-border">
                                <span>  关卡数</span>
                                <span class="num"> {{card.level_count}}</span>
                        </div>
                    </div>
                    <div class="wordsNum">
                            <div class="borders">
                                <span> 单词数</span>
                                <span class="num"> {{card.word_count}}</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </template>
     </div>    
  </div>
</template>

<script>
import { getWordAppContent } from "../api/wordsManage";
export default {
  data() {
    return {
      list: []
    };
  },
  methods: {
    gotoEditer(){
      console.log('%%%%%%%%%%%%%%%&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&')
    },
    gotoDatabaseEditer(index){
            this.$router.push({name:'user',params: {id: index}})
    },
    getList() {
      let self = this;
      getWordAppContent(self.list, 1, "").then(({ data }) => {
        self.list = self.list.concat(data);
        console.log(data);
      });
    }
  },
  mounted() {
    this.getList();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.lessonCard {
  width: 100%;
  height: 100%;
  padding: 20px;
  padding-top: 0;
  box-sizing: border-box;
  overflow: auto;
  padding-bottom: 80px;
}

/* 顶部通栏 */
.lessonCard .lessonCard-top {
  width: 100%;
  padding: 10px;
  background-color: #fff;
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  align-items: center;
  border-radius: 5px;
  margin-bottom: 20px;
}
.el-input {
  position: relative;
  font-size: 14px;
  display: inline-block;
  width: 258px;
}
.lessonCard-top .btn {
  margin-left: 10px;
  cursor: pointer;
}
.lessonCard-top .commen-style,
.lessonCard-top .allBook {
  margin-left: 20px;
  cursor: pointer;
}
.lessonCard-top .highLight {
  color: #319bd7;
}
.lessonCard-top .min,
.lessonCard-top .end {
  margin-left: 40px;
  cursor: pointer;
}

/* 卡片展示区 */
.lessonCard-content {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
  width: 100%;
  box-sizing: border-box;
  padding-right: 180px;
  background-color: #fff;
}
.lessonCard-content .card-item {
  width: 50%;
  text-align: center;
  padding: 20px;
  box-sizing: border-box;
  background-color: #fff;
}
.lessonCard-content .card-item .wrap-border {
  box-shadow: 0 0px 10px rgba(0, 0, 0, 0.2);
  position: relative;
}
.lessonCard-content .card-item .wrap-border .card-adsFlag {
  position: absolute;
  top: 5px;
  left: -5px;
}
img {
  display: block;
}
.lessonCard-content .card-item .wrap-border .postal-card {
  padding: 0 20px;
  padding-top: 30px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  box-sizing: border-box;
  margin-bottom: 50px;
  cursor: pointer;
}
.lessonCard-content .card-item .wrap-border .postal-card .card-left {
  width: 40%;
  padding: 0 20px;
  border: 1px solid #eee;
  margin-right: 20px;
}
.lessonCard-content .card-item .wrap-border .postal-card .card-left .book-bg {
  /* width: 100%;
  height: 100%; */
  min-width: 112px;
  min-height: 150px;
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.lessonCard-content .card-item .wrap-border .postal-card .card-right {
  font-size: 13px;
  color: #333;
  text-align: left;
  line-height: 25px;
  width:55%;
}
.lessonCard-content
  .card-item
  .wrap-border
  .postal-card
  .card-right
  .cardRight-middle {
  /* line-height: 60px; */
  font-size: 12px;
  color: #666;
  margin: 10px 0;
  line-height: 12px;
}

.lessonCard-content
  .card-item
  .wrap-border
  .postal-card
  .card-right
  .cardRight-bottom {
  /* line-height: 60px; */
  font-size: 16px;
  color: #333;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.lessonCard-content
  .card-item
  .wrap-border
  .postal-card
  .card-right
  .cardRight-bottom
  .tip {
    width:70%;
    font-size: 10px;
    margin-left: 20px;
    /* flex: 40%; */
    color: #e88c12;
    text-align: left;

}

.lessonCard-content
  .card-item
  .wrap-border
  .postal-card
  .card-right
  .cardRight-bottom
  .tip .tip-item{
    border: 1px solid #ff880a;
    border-radius: 4px;
    line-height: 18px;
    padding: 0 2px;
    display:inline-block;
    margin-right: 10px;
    text-align: center;
}
.lessonCard-content .card-item .wrap-border .wrapCard-tottom {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding: 10px 0;
  background-color: #f4fafd;
}
.lessonCard-content .card-item .wrap-border .wrapCard-tottom > div {
  width: 50%;
}
.lessonCard-content .card-item .wrap-border .wrapCard-tottom div.caseNum {
  width: 50%;
}
.rightSide-border {
  border-right: 1px solid #ddd;
  height: 50px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.lessonCard-content .card-item .wrap-border .wrapCard-tottom .borders span {
  display: block;
  font-size: 12px;
  color: #999;
  line-height: 18px;
}
.lessonCard-content .card-item .wrap-border .wrapCard-tottom .borders span.num {
  display: block;
  font-size: 18px;
  color: #333;
  line-height: 25px;
}
.lessonCard-content .card-item .wrap-border .card-tottom {
  width: 100%;
  border: none;
  line-height: 50px;
  font-size: 16px;
  color: #319bd7;
  padding: 10px 0;
  background-color: #f4fafd;
  cursor: pointer;
}
.just-border {
  height: 50px;
  box-sizing: border-box;
  display: flex;
  flex-direction: center;
  justify-content: center;
  align-items: center;
}
</style>



