<template>
  <div class="user">
      {{name}}
  </div>
</template>

<script>
import {getLevels}  from '../api/caseManage'
export default {
  data () {
      return {
          name:"ycc",
          word_app_content_id:''
      }
  },
  methods: {
      getCase(){
          let self=this;
          getLevels(self.word_app_content_id).then( ({data})=>{
              console.log(data);
          } )
      }
  },
  mounted () {
      this.getCase();
  },
  created () {
      this.word_app_content_id = this.$route.params.id;
  }
}
</script>


<style scoped>
.user{
    color: red;
}
</style>





